  main{
    min-height: 100vh;
    background-image: linear-gradient (to bottom,rgba(0,0,0,0.2),rgba(0,0,0,0.75));
    padding: 40px;
  }
  
  .search-box {
    width: 100%;
    margin: 0 0 75px;
  }
  .searching{
    margin-top: -114px;
    margin-left: 15px;
  }
  
  .search-box .search-bar{
    display: block;
    width: 100%;
    padding: 15px;
    appearance: none;
    background: none;
    border: 1px solid;
    outline: none;
    background-color: rgba(255,255,255,0.5);
    border-radius: 0px 0px 16px 16px;
    margin-top: -41px;
    
    box-shadow: 0px 5px rgba(0,0,0,0.2);
    color: #313131;
    font-size: 20px;
    transition: 0.4s ease;
  
  }
  
  .search-box .search-bar:focus{
   background-color: rgba(255,255,255,0.75);
  }
  
  .location-box .location{
    color: #800b0b;
    font-size: 32px;
    font-weight: 500;
    text-align: center;
    text-shadow: rgba(50,50,70,0.5);
  
  }
  .location-box .date{
    color: #800b0b;
    margin-top: 2px;
    font-size: 20px;
    font-weight: 300;
    font-style: italic;
    text-align: center;
    text-shadow: rgba(50,50,70,0.5);
  }
  
  .weather-box{
    text-align: center;
  }
  
  .weather-box .temp{
   position: relative;
   display: inline-block;
   margin: 30px auto;
   background-color: rgba(255,255,255,0.2);
   border-radius: 15px 25px;
   padding: 15px 25px;
  
   color: #800b0b;
   font-size: 102px;
   font-weight: 900;
   text-shadow: rgba(50,50,70,0.5);
   text-align: center;
   box-shadow: 3px 6px rgba(0,0,0,0.2);
  }
  .feelslike{
    color: #800b08;
    font-size: 25px;
    font-weight: 600;
    text-shadow: 2px 2px rgba(50,50,70,0.5);
  }
  .weather-box .weather{
    color: #800b08;
    font-size: 48px;
    font-weight: 700;
    text-shadow: 3px 3px rgba(50,50,70,0.5);
  }